 @use"var"as vars;

body {
  background-color: vars.$bg-color;
  font-family: vars.$font-family;
  margin: 0;
  padding: 0;
  color: vars.$color;
}

.app {
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
 
}
